.quickSearchContainer{
    margin-left:115px;
    margin-top:50px;

}
.quickSearchHeading{
    text-align: left;
    color:rgb(173, 20, 20);
    font-size:30px;
    font-weight: bold;
    font-family: "Times New Roman", Times, serif;
}
.quickSearchSubHeading{
    color:rgb(241, 223, 223);
    font-size:20px;
    font-weight: normal;
    font-family: "Times New Roman", Times, serif;
    line-height: .01;
}
.tileContainer{
    height:180px;
    width:28%;
    display: inline-block;
    margin-top: 30px;
    margin-bottom: 30px;
    margin-right: 3%;
    box-shadow: 0px 0px 15px black;
    border-radius: 1.5px;
}

.tileComponent1{
    width:45%;
    float: left;
    height:180px
    
}
.tileComponent1 img{
    width:100%;
    float: left;
    height:180px
    
}

.tileComponent2{
    width:46%;
    float: left;
    margin-top: 20px;
    margin-left:20px;
}
.componentHeading{
    text-align: left;
    color:#192f60;
    font-size:25px;
    font-weight: bold;
}

.componentSubHeading{
    color:rgb(241, 234, 234);
    font-size: 18px;
    line-height: 1;
}

.tileContainer:hover{
    /*transform: rotate(0deg)*/
    transform: scale(1.1,1.1);
    
}
@media screen and (max-width:375px){
    .tileContainer{
        height:180px;
        width:28%;
        display: inline;
        margin-top: 30px;
        margin-bottom: 30px;
        margin-right: 3%;
        box-shadow: 0px 0px 15px black;
        border-radius: 1.5px;
    }
    .componentHeading{
        text-align: left;
        color:#192f60;
        font-size:15px;
        font-weight: bold;
    }
    .tileComponent1 img{
        width:100%;
        float: left;
        height:180px
        
    }

}

