.first{
    /* border: 2px solid black; */
    height:300px;
    width:80%;
    float: left;
    margin-left: 8%;
    margin-top: 2%; 
    margin-bottom: 8%;
    box-shadow: 5px 4px 4px rgb(59, 55, 55);
    
}

.second{
    margin-top: 2%; 
}


body{
    margin: auto;
    background: url('https://source.unsplash.com/1600x1800?dark,food,blur');
    background-repeat: no-repeat;
}

#header{
    width:100%;
    height:50px;
    background-color: tomato;
}

.logo{
    font-size:30px;
    margin-left: 5%;
    color: white;
    text-decoration: none;
}
.logoname{
    margin-right: 5%;
}
.image{
    width:20%;
    height:100%;
    float: left;
    margin-right: 5rem;
}

.maindiv{
    width:100%;
    height:70%;
}

.moredetails{
    width:100%;
    height:30%;
    
}
.imgclass{
    height:150px;
    width:100%;
    border-radius: 5px;
    /* margin-left: 5%; */
}
.details h1{
    color:tomato;
    font-family: "Times New Roman", Times, serif;
    text-decoration: none;
    transition: all .5s ease;
    
}
.details h1:hover{
    
    color: rgb(216, 17, 17);;
}

.moredetails h4{
    color:#ffff;
    font-family: "Times New Roman", Times, serif;
}
.moredetails p{
    color: rgb(223, 217, 217);
    font-family: "Times New Roman", Times, serif;
}
.details p{
    color: rgb(223, 217, 217);
    font-family: "Times New Roman", Times, serif;
}
.filters{
    color: rgb(243, 229, 229);
    /* border: 2px solid black; */
    margin: 20px 0px 0px 30px;
    width: 15rem;
    font-family: "Times New Roman", Times, serif;
    cursor: pointer;
}
.cuisine{
    margin: 20px 0px 0px 30px;
    font-family: "Times New Roman", Times, serif;
    color:rgb(243, 229, 229);
}
@media screen and (max-width:1000px){
    .first{
        /* border: 2px solid black; */
        width:70%;
        float: right;
        margin-right: 20%;
        margin-top: 2%;
        box-shadow: 1px 1px 5px #888;
        padding: 1%;
        border-radius: 5px;
        
    }
    .maindiv{
        width:100%;
        height:50%;
    }
    #listingbox{
        /* margin: 0;
        float: right; */
        position: absolute;
        top: 8%;
        left: 10%;
    }
    .details h1{
        color:tomato;
        font-family: "Times New Roman", Times, serif;
        text-decoration: none;
        transition: all .5s ease;
        font-size: 25px;
        
        
    }
    .moredetails p{
        color: rgb(223, 217, 217);
        font-family: "Times New Roman", Times, serif;
    }
    .first.moredetails.locality1{
        color: rgb(223, 217, 217);
        font-family: "Times New Roman", Times, serif;
        margin-right: 5%;
    }

}