body{
    margin: auto;
}
.imageContainer{
    /* background: url('https://source.unsplash.com/600x300/?indian,meal,bar'); */
    background-repeat: no-repeat;
    background-size: cover;
    border: 1px solid gray;
}

#logo{
    text-align: center;
    color:tomato;
    font-size:90px;
    background:white;
    height:150px;
    width:150px;
    border-radius: 60%;
    margin:auto;
    margin-top:80px;
    margin-bottom:40px;
    font-family: "Times New Roman", Times, serif;
}

#heading{
    text-align: center;
    color:white;
    font-size: 36px;
    font-family: "Times New Roman", Times, serif;
}

.locationSelector{
    margin-top:40px;
    margin-bottom:40px;
    text-align:center;
}

.dropdown{
    height:50px;
    width:250px;
    font-size:20px
}

.fb{
    color:#4267b2;
    text-decoration:none
}
.myfont{
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-weight: bold;
}
.yt{
    color:#FF0000;
    text-decoration:none;
}
.linkin{
    color:#0F89AD;
    text-decoration:none;
}

.social_logo{
    height:50px;
    width:50px;
    display: flex;
    float: right;
    margin-top: 1%;
    margin-right: 2%;
}
@media screen and (max-width:375){
    .dropdown{
        height:35px;
        width:150px;
        font-size:10px
    }
}